.brands {
  padding: 0;
  margin: 0 auto;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1280px;
}

.brands__subtitle {
  align-items: center;
  font-size: 22px;
  line-height: 1.2;
}

.brands__nenu {
  list-style-type: none;
  counter-reset: num;
  margin: 0;
  padding: 0;
}

.brands__list {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  /* list-style: none; */
}

.brands__list::before {
  content: counter(num) '.'; /* Добавляем к числам точку */
  counter-increment: num; /* Задаём имя счетчика */
  color: rgb(100%, 100%, 100%); /* Менять цвет */
  display: inline-block; /* Расстояние между точкой и текстом */
  width: 1em; /* Задать значение расстояния */
  font-size: 15px;
  margin-top: 20px;
}

.brands__paragraph {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  margin: 0;
  padding: 0;
  border: 0;
  margin-top: 20px;
  text-align: left;
  padding-left: 5px;
}

.catalogs {
  margin-top: 30px;
}

.catalog-service {
  display: flex;
  flex-direction: column;
  margin-top: 25px;
}

.catalog-service__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog-service__title {
  margin-top: 20px;
}

.catalog__nenu {
  padding: 0;
  margin: 0;
}

.catalog__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
}

.catalog__list::before {
  content: '*'; /* юникод точки */
  color: rgb(100%, 100%, 100%); /* Менять цвет */
  font-weight: bold; /* Менять жирность */
  display: inline-block; /* Расстояние между точкой и текстом */
  width: 1em; /* Задать значение расстояния */
  font-size: 15px;
  padding-top: 20px;
}

.catalog__paragraph {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  margin: 0;
  padding: 0;
  border: 0;
  margin-top: 20px;
  text-align: left;
  padding-left: 5px;
}

/* .package__price {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  margin-top: 20px;
} */

.catalog-service__link {
  color: #928763;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-decoration: none;
}

.catalog-service__arrow {
  padding-left: 3px;
  color: #928763;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
}

.catalog-service__price {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0;
  margin: 0;
  margin-top: 20px;
}
.catalog-service__subtitle {
  text-align: left;
}

@media (min-width: 768px) {
  .catalogs {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }

  .brands__subtitle {
    font-size: 36px;
  }

  .catalog-service {
    width: calc(100% / 3);
    padding-left: 18px;
    align-items: flex-start;
  }

  .catalog-service:first-child {
    padding-left: 0;
  }

  .catalog-service__title {
    text-align: start;
  }
}

@media (min-width: 1200px) {
  .catalog-service {
    padding-left: 30px;
  }
}
